ht:700;margin:0}.wed-image-form[data-v-13ff9bbe]{grid-column-end:3;grid-column-start:1}.gallery_upload_form[data-v-13ff9bbe]{height:-moz-max-content;height:max-content}.gallery_album_form[data-v-13ff9bbe],.gallery_upload_form[data-v-13ff9bbe]{-webkit-backdrop-filter:blur(4px);backdrop-filter:blur(4px);background:hsla(0,0%,89%,.6);border-radius:17.8211px}.gallery_upload_form1>label[data-v-13ff9bbe]{cursor:pointer;display:flex;flex-direction:column;height:100%;justify-content:center;width:100%}.gallery_upload_form1>label>span[data-v-13ff9bbe]{align-self:center}.gallery_upload_form>label[data-v-13ff9bbe]{cursor:pointer;display:flex;flex-direction:column;height:100%;justify-content:center;width:100%}.gallery_upload_form>label>span[data-v-13ff9bbe]{align-self:center}@media screen and (max-width:769px){.wed-host-gallery[data-v-13ff9bbe]{padding:14px 0!important}}@media screen and (max-width:576px){.wed-host-gallery-items>div[data-v-13ff9bbe]{grid-column-gap:1em;grid-row-gap:1.5em;display:grid;grid-auto-rows:max-content;grid-template-columns:repeat(2,1fr)!important;padding:1em}}", "",{"version":3,"sources":["webpack://./resources/js/components/host/Gallery.vue"],"names":[],"mappings":"AA6hBA,mCACA,qBAAA,CACA,cAAA,CACA,cACA,CAEA,mDACA,YAAA,CACA,sBACA,CAEA,0CAGA,yCAAA,CADA,uBAAA,CADA,yBAGA,CAEA,uDAWA,qCAAA,CADA,oCAAA,CAJA,aAAA,CAMA,cAAA,CALA,MAAA,CANA,kBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAIA,aAAA,CADA,iBAKA,CACA,kEACA,iBACA,CACA,yCACA,kBACA,CACA,6CAKA,mBAAA,CACA,kBAAA,CALA,YAAA,CAEA,0BAAA,CADA,yDAAA,CAEA,WAGA,CACA,wDAIA,kBAAA,CADA,gBAAA,CADA,iBAGA,CACA,iGAQA,kBAAA,CAFA,WAAA,CACA,mBAAA,CAAA,gBAAA,CAJA,iBAAA,CACA,KAAA,CACA,UAAA,CAIA,UACA,CACA,8CAQA,cAAA,CADA,eAAA,CAEA,gBAAA,CANA,UAAA,CADA,QASA,CACA,6FAFA,UAAA,CALA,kBAAA,CACA,iBAAA,CALA,iBAAA,CAGA,WAmBA,CAXA,+CAEA,WAAA,CAMA,cAAA,CADA,eAAA,CAJA,SAAA,CAMA,gBAEA,CACA,6BAKA,mBAAA,CACA,kBAAA,CALA,YAAA,CAEA,0BAAA,CADA,yDAAA,CAEA,WAGA,CAEA,+BACA,gBAAA,CACA,iBACA,CAEA,kCAEA,aAAA,CACA,eAAA,CAFA,QAGA,CASA,iCAEA,iBAAA,CADA,mBAEA,CAEA,sCAIA,uBAAA,CAAA,kBACA,CAEA,2EALA,iCAAA,CAAA,yBAAA,CADA,4BAAA,CAEA,uBAQA,CAMA,6CAKA,cAAA,CAJA,YAAA,CAKA,qBAAA,CAFA,WAAA,CAFA,sBAAA,CACA,UAIA,CAEA,kDACA,iBACA,CAEA,4CAKA,cAAA,CAJA,YAAA,CAKA,qBAAA,CAFA,WAAA,CAFA,sBAAA,CACA,UAIA,CAEA,iDACA,iBACA,CACA,oCACA,mCACA,wBACA,CACA,CACA,oCACA,6CAKA,mBAAA,CACA,kBAAA,CALA,YAAA,CAEA,0BAAA,CADA,6CAAA,CAEA,WAGA,CACA","sourcesContent":["\n<template>\n    <div>\n        <div class=\"gallery-all-cont wed-host-gallery container-md\">\n            <div>\n                <div\n                    @click=\"\n                        activeGallery = 'Pictures';\n                        clickedAlbum = [];\n                    \"\n                    :class=\"{\n                        'wed-host-gallery-active': activeGallery == 'Pictures',\n                    }\"\n                >\n                    Pictures\n                </div>\n                <div\n                    @click=\"\n                        activeGallery = 'Albums';\n                        clickedAlbum = [];\n                    \"\n                    :class=\"{\n                        'wed-host-gallery-active': activeGallery == 'Albums',\n                    }\"\n                >\n                    Albums\n                </div>\n                <div\n                    @click=\"\n                        activeGallery = 'Videos';\n                        clickedAlbum = [];\n                    \"\n                    :class=\"{\n                        'wed-host-gallery-active': activeGallery == 'Videos',\n                    }\"\n                >\n                    Videos\n                </div>\n            </div>\n            <div class=\"wed-host-gallery-items\">\n                <div v-if=\"activeGallery == 'Pictures'\">\n                    <form\n                        @submit.prevent=\"sendPictures($event)\"\n                        class=\"wed-image-form gallery_upload_form\"\n                    >\n                        <div class=\"p-3\">\n                            <div class=\"row mb-2 p-2\">\n                                <label for=\"image\" class=\"form-label\">Add Images</label>\n                                <input\n                                    type=\"file\"\n                                    name=\"galleryImage[]\"\n                                    class=\"form-control form-control-sm\"\n                                    id=\"\"\n                                    multiple\n                                    @change=\"uploadImages($event)\"\n                                />\n                                <span\n                                    v-if=\"errorsSubmit && errorsSubmit.galleryImage\"\n                                    class=\"errMsg\"\n                                >{{ errorsSubmit.galleryImage[0] }}</span>\n                                <div>{{ uploadMultiMessage }}</div>\n                            </div>\n                            <div class=\"row p-3\">\n                                <button\n                                    type=\"submit\"\n                                    class=\"btn btn-sm btn-primary\"\n                                    :disabled=\"disablePictures\"\n                                >\n                                    Upload Images\n                                </button>\n                            </div>\n                        </div>\n                    </form>\n                    <div\n                        v-for=\"(item, index) in nonAlbumImages\"\n                        :key=\"index\"\n                        class=\"img-cont\"\n                    >\n                        <img\n                            :src=\"'/files/' + invitations.id + '/' + item.imageName\"\n                            alt=\"\"\n                        />\n                    </div>\n                </div>\n                <div\n                    id=\"all-albums\"\n                    v-if=\"activeGallery == 'Albums' && clickedAlbum.length == 0\"\n                >\n                    <form\n                        @submit.prevent=\"createAlbum($event)\"\n                        class=\"wed-image-form gallery_upload_form\"\n                    >\n                        <div class=\"p-3\">\n                            <div class=\"row p-2\">\n                                <label for=\"album\" class=\"form-label\">\n                                    Album Name</label\n                                >\n                                <input\n                                    type=\"text\"\n                                    name=\"album\"\n                                    class=\"form-control\"\n                                    v-model=\"albumName\"\n                                    required\n                                />\n                            </div>\n                            <div class=\"row mb-2 p-2\">\n                                <label for=\"image\" class=\"form-label\">Add Images</label>\n                                <input\n                                    type=\"file\"\n                                    name=\"galleryImage[]\"\n                                    class=\"form-control form-control-sm\"\n                                    id=\"\"\n                                    multiple\n                                    accept=\"image/*\"\n                                    @change=\"uploadImages($event)\"\n                                />\n                                <span\n                                    v-if=\"errorsSubmit && errorsSubmit.galleryImage\"\n                                    class=\"errMsg\"\n                                >{{ errorsSubmit.galleryImage[0] }}</span>\n                                <div>{{ uploadMultiMessage }}</div>\n                            </div>\n                            <div class=\"row p-3\">\n                                <button\n                                    type=\"submit\"\n                                    class=\"btn btn-sm btn-primary\"\n                                    :disabled=\"disablePictures\"\n                                >\n                                    Create Album\n                                </button>\n                            </div>\n                        </div>\n                    </form>\n                    <div\n                        v-for=\"(item, index) in allAlbums\"\n                        :key=\"index\"\n                        @click=\"clickedAlbum = item.images; albumName = item.name\"\n                        class=\"album-cont\"\n                    >\n                        <span>\n                            {{ item.images.length }}\n                            <img src=\"/assets/guestInvi/bi_image-fill.svg\" />\n                        </span>\n                        <img src=\"/assets/guestInvi/Frame-5602-1.png\" alt=\"\" />\n                        <span> {{ item.name }} </span>\n                    </div>\n                </div>\n                <div v-if=\"activeGallery == 'Albums' && clickedAlbum.length > 0\" class=\"album-pics\">\n                    <div class=\"album-header\">\n                        <button @click=\"clickedAlbum = []; albumName = null\" class=\"btn btn-sm btn-secondary mb-3\">\n                            ← Back to Albums\n                        </button>\n                        <h4>{{ albumName }}</h4>\n                    </div>\n                    <div\n                        v-for=\"single in clickedAlbum\"\n                        :key=\"single.imageName\"\n                        class=\"img-cont\"\n                    >\n                        <img\n                            :src=\"\n                                '/files/' +\n                                invitations.id +\n                                '/' +\n                                single.imageName\n                            \"\n                            alt=\"\"\n                        />\n                    </div>\n                </div>\n                <div v-if=\"activeGallery == 'Videos'\">\n                    <form\n                        class=\"wed-image-form gallery_upload_form\"\n                        @submit.prevent=\"sendVideos($event)\"\n                    >\n                        <div class=\"p-3\">\n                            <div class=\"row mb-2 p-2\">\n                                <label for=\"video\" class=\"form-label\"\n                                    >Add Video</label\n                                >\n                                <input\n                                    type=\"file\"\n                                    name=\"galleryVideo[]\"\n                                    class=\"form-control form-control-sm\"\n                                    id=\"\"\n                                    multiple\n                                    accept=\"video/*\"\n                                    @change=\"uploadVideos($event)\"\n                                />\n                                <span\n                                    v-if=\"\n                                        errorsSubmit &&\n                                        errorsSubmit.galleryVideo\n                                    \"\n                                    class=\"errMsg\"\n                                    >{{ errorsSubmit.galleryVideo[0] }}</span\n                                >\n                                <div>{{ uploadMultiMessage }}</div>\n                            </div>\n                            <div class=\"row p-3\">\n                                <button\n                                    type=\"submit\"\n                                    :disabled=\"disablePictures\"\n                                    class=\"btn btn-sm btn-primary\"\n                                >\n                                    Upload Video\n                                </button>\n                            </div>\n                        </div>\n                    </form>\n                    <div\n                        v-for=\"(item, index) in allVideos\"\n                        :key=\"index\"\n                        class=\"img-cont\"\n                    >\n                        <video controls>\n                            <source\n                                :src=\"\n                                    '/videos/' +\n                                    invitations.id +\n                                    '/' +\n                                    item.name\n                                \"\n                            />\n                            Your browser does not support the video tag.\n                        </video>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <flashMessage :message=\"message\"></flashMessage>\n    </div>\n</template>\n\n<script>\nimport flashMessage from \"../FlashMessage.vue\";\n\nexport default {\n    components: {\n        flashMessage,\n    },\n    props: [\"albums\", \"galleries\", \"videos\", \"host\", \"invitations\"],\n    data() {\n        return {\n            activeGallery: \"Pictures\",\n            clickedAlbum: [],\n            message: null,\n            uploadMultiImage: [],\n            uploadMultiMessage: null,\n            albumName: null,\n            allAlbums: this.albums,\n            allGalleries: this.galleries,\n            allVideos: this.videos,\n            disablePictures: false,\n            errorsSubmit: null,\n        };\n    },\n    computed: {\n        nonAlbumImages() {\n            // Filter out images that have an album\n            return this.allGalleries.filter(image => !image.album);\n        }\n    },\n    methods: {\n        uploadImages(e) {\n            const files = e.target.files;\n            const validFiles = [];\n            const invalidFiles = [];\n\n            // Check each file\n            for (let i = 0; i < files.length; i++) {\n                const file = files[i];\n\n                // Check file type\n                const fileType = file.type;\n                const validTypes = ['image/jpeg', 'image/png', 'image/jpg', 'image/gif', 'image/svg+xml'];\n\n                // Check file size (10MB = 10485760 bytes for albums, 512KB for pictures)\n                const fileSize = file.size;\n                const maxSize = this.activeGallery === 'Albums' ? 10485760 : 524288;\n\n                if (validTypes.includes(fileType) && fileSize <= maxSize) {\n                    validFiles.push(file);\n                } else {\n                    invalidFiles.push({\n                        name: file.name,\n                        reason: !validTypes.includes(fileType) ? 'Invalid file type' : 'File too large'\n                    });\n                }\n            }\n\n            // Update the component state\n            this.uploadMultiImage = validFiles;\n\n            if (invalidFiles.length > 0) {\n                const reasons = invalidFiles.map(f => `${f.name}: ${f.reason}`).join(', ');\n                this.uploadMultiMessage = `${validFiles.length} valid files. Skipped: ${reasons}`;\n            } else {\n                this.uploadMultiMessage = validFiles.length > 0 ?\n                    `${validFiles.length} Files Selected!` :\n                    'No valid files selected';\n            }\n\n            // Disable the submit button if no valid files\n            this.disablePictures = validFiles.length === 0;\n        },\n        uploadVideos(e) {\n            const files = e.target.files;\n            const validFiles = [];\n            const invalidFiles = [];\n            \n            for (let i = 0; i < files.length; i++) {\n                const file = files[i];\n                const fileType = file.type;\n                const validTypes = ['video/mp4', 'video/avi', 'video/mpeg', 'video/quicktime', 'video/webm', 'video/ogg'];\n                \n                if (validTypes.includes(fileType) && file.size <= 10485760) {\n                    validFiles.push(file);\n                } else {\n                    invalidFiles.push({\n                        name: file.name,\n                        reason: !validTypes.includes(fileType) ? 'Invalid file type' : 'File too large (max 10MB)'\n                    });\n                }\n            }\n\n            // Update the component state\n            this.uploadMultiImage = validFiles;\n\n            if (invalidFiles.length > 0) {\n                const reasons = invalidFiles.map(f => `${f.name}: ${f.reason}`).join(', ');\n                this.uploadMultiMessage = `${validFiles.length} valid files. Skipped: ${reasons}`;\n            } else {\n                this.uploadMultiMessage = validFiles.length > 0 ?\n                    `${validFiles.length} Videos Selected!` :\n                    'No valid files selected';\n            }\n\n            // Disable the submit button if no valid files\n            this.disablePictures = validFiles.length === 0;\n        },\n        createAlbum(e) {\n            // This is essentially the same as sendPictures but specifically for album creation\n            let _this = this;\n            _this.disablePictures = true;\n            _this.errorsSubmit = null;\n            let formData = new FormData();\n            \n            formData.append(\"album\", _this.albumName);\n            let images = null;\n            if (_this.uploadMultiImage.length > 0) {\n                images = [..._this.uploadMultiImage];\n                images.forEach(function (item, index) {\n                    formData.append(\"galleryImage[\" + index + \"]\", item);\n                });\n            }\n            \n            axios({\n                method: \"POST\",\n                url: route(\"addAlbum\", _this.host),\n                data: formData,\n                headers: { \"content-type\": \"multipart/form-data\" },\n            })\n                .then((response) => {\n                    _this.allAlbums = response.data[0];\n                    _this.allGalleries = response.data[1];\n                    _this.allVideos = response.data[2];\n                    _this.message = \"Album Created Successfully!\";\n                    _this.uploadMultiMessage = null;\n                    _this.uploadMultiImage = [];\n                    _this.albumName = null;\n                    _this.disablePictures = false;\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                })\n                .catch((error) => {\n                    _this.disablePictures = false;\n                    _this.errorsSubmit = {\n                        galleryImage: Object.values(\n                            error.response.data?.errors\n                        )[0],\n                    };\n                });\n        },\n        sendPictures(e) {\n            let _this = this;\n            _this.disablePictures = true;\n            _this.errorsSubmit = null;\n            let formData = new FormData();\n            let link = null;\n\n            if (_this.activeGallery == \"Pictures\") {\n                let images = null;\n                if (_this.uploadMultiImage.length > 0) {\n                    images = [..._this.uploadMultiImage];\n                    images.forEach(function (item, index) {\n                        formData.append(\"galleryImage[\" + index + \"]\", item);\n                    });\n                }\n                // Don't send albumId for Pictures section\n                // The controller will handle it as NULL\n                link = route(\"hostgallery.store\", _this.host);\n            }\n\n            let meth = \"POST\";\n\n            // Debug the form data before sending\n            console.log(\"Form data contents:\");\n            for (let pair of formData.entries()) {\n                console.log(pair[0] + ': ' + (pair[1] instanceof File ?\n                    `File: ${pair[1].name}, size: ${pair[1].size}` : pair[1]));\n            }\n\n            axios({\n                method: meth,\n                url: link,\n                data: formData,\n                headers: { \"content-type\": \"multipart/form-data\" },\n            })\n                .then((response) => {\n                    _this.allAlbums = response.data[0];\n                    _this.allGalleries = response.data[1];\n                    _this.allVideos = response.data[2];\n                    _this.message = \"Images Uploaded Successfully!\";\n                    _this.uploadMultiMessage = null;\n                    _this.uploadMultiImage = [];\n                    _this.disablePictures = false;\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                })\n                .catch((error) => {\n                    _this.disablePictures = false;\n                    _this.handleUploadError(error, \"Pictures\");\n                });\n        },\n        sendVideos(e) {\n            let _this = this;\n            _this.disablePictures = true;\n            _this.errorsSubmit = null;\n            let formData = new FormData();\n            \n            if (_this.uploadMultiImage.length > 0) {\n                let videos = [..._this.uploadMultiImage];\n                videos.forEach(function (item, index) {\n                    formData.append(\"galleryVideo[\" + index + \"]\", item);\n                });\n            }\n            let link = route(\"hostvideoUpload\", _this.host);\n            let meth = \"POST\";\n\n            // Debug the form data before sending\n            console.log(\"Video form data contents:\");\n            for (let pair of formData.entries()) {\n                console.log(pair[0] + ': ' + (pair[1] instanceof File ?\n                    `File: ${pair[1].name}, size: ${pair[1].size}` : pair[1]));\n            }\n\n            axios({\n                method: meth,\n                url: link,\n                data: formData,\n                headers: { \"content-type\": \"multipart/form-data\" },\n            })\n                .then((response) => {\n                    _this.allAlbums = response.data[0];\n                    _this.allGalleries = response.data[1];\n                    _this.allVideos = response.data[2];\n                    _this.message = \"Videos Uploaded Successfully!\";\n                    _this.uploadMultiMessage = null;\n                    _this.uploadMultiImage = [];\n                    _this.disablePictures = false;\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                })\n                .catch((error) => {\n                    _this.disablePictures = false;\n                    _this.handleUploadError(error, \"Videos\");\n                });\n        },\n        handleUploadError(error, type) {\n            let _this = this;\n            // Log the full error for debugging\n            console.error(\"Full error response:\", error.response);\n\n            // For 500 errors, show a more helpful message\n            if (error.response && error.response.status === 500) {\n                _this.message = \"Server error occurred. Please check with administrator.\";\n                console.error(\"Server error details:\", error.response.data);\n\n                // You could also add a more specific message based on common issues\n                if (error.response.data && error.response.data.message) {\n                    if (error.response.data.message.includes(\"Permission denied\")) {\n                        _this.message = \"Server error: Permission denied when saving files.\";\n                    } else if (error.response.data.message.includes(\"No such file or directory\")) {\n                        _this.message = \"Server error: Upload directory does not exist.\";\n                    }\n                }\n            } else {\n                // Handle validation errors as before\n                console.error(\"Validation errors:\", error.response?.data?.errors);\n\n                if (type == \"Pictures\") {\n                    if (error.response && error.response.data && error.response.data.errors) {\n                        // Get the first error message from each field\n                        const errorMessages = {};\n                        Object.keys(error.response.data.errors).forEach(field => {\n                            errorMessages[field] = error.response.data.errors[field][0];\n                        });\n                        console.log(\"Validation error messages:\", errorMessages);\n\n                        _this.errorsSubmit = {\n                            galleryImage: errorMessages.galleryImage || [\"Unknown validation error\"]\n                        };\n                    } else {\n                        _this.errorsSubmit = {\n                            galleryImage: [\"An error occurred while uploading images\"]\n                        };\n                    }\n                }\n                if (type == \"Videos\") {\n                    _this.errorsSubmit = {\n                        galleryVideo: error.response && error.response.data && error.response.data.errors\n                            ? Object.values(error.response.data.errors)[0]\n                            : [\"An error occurred while uploading videos\"]\n                    };\n                }\n            }\n\n            setTimeout(function () {\n                _this.message = null;\n            }, 5000);\n        },\n    },\n};\n</script>\n\n<style scoped>\n/* Gallery all css start */\n.wed-host-gallery {\n    background-color: white;\n    margin-top: 2em;\n    padding: 10px 0;\n}\n\n.wed-host-gallery > div:nth-child(1) {\n    display: flex;\n    justify-content: center;\n}\n\n.wed-host-gallery-active {\n    font-weight: bold !important;\n    color: #7f004b !important;\n    border-bottom: 4px solid #7f004b !important;\n}\n\n.wed-host-gallery > div:nth-child(1) > div {\n    font-family: Roboto;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 17.8211px;\n    line-height: 21px;\n    color: #968585;\n    flex: 1;\n    text-align: center;\n    padding: 2em 0;\n    border-right: 0.891053px solid #cbc7c7;\n    border-bottom: 0.891053px solid #cbc7c7;\n    cursor: pointer;\n}\n.wed-host-gallery > div:nth-child(1) > div:last-child {\n    border-right: none;\n}\n.wed-host-gallery-items {\n    padding-bottom: 1em;\n}\n.wed-host-gallery-items > div {\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));\n    grid-auto-rows: max-content;\n    padding: 1em;\n    grid-column-gap: 1em;\n    grid-row-gap: 1.5em;\n}\n.img-cont,\n.album-cont {\n    position: relative;\n    padding-top: 100%;\n    border-radius: 20px;\n}\n.img-cont > img,\n.img-cont > video,\n.album-cont > img {\n    position: absolute;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    border-radius: 20px;\n    z-index: 99;\n}\n.album-cont > span:nth-child(1) {\n    position: absolute;\n    top: 15px;\n    right: 15px;\n    z-index: 999;\n    font-family: \"Roboto\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 16px;\n    line-height: 19px;\n    color: #ffffff;\n}\n.album-cont > span:nth-child(3) {\n    position: absolute;\n    bottom: 15px;\n    left: 15px;\n    z-index: 999;\n    font-family: \"Roboto\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 15px;\n    line-height: 18px;\n    color: #ffffff;\n}\n.album-pics {\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));\n    grid-auto-rows: max-content;\n    padding: 1em;\n    grid-column-gap: 1em;\n    grid-row-gap: 1.5em;\n}\n\n.album-header 